/* ==================================================
   CONTENITORE PRINCIPALE
================================================== */
.slot-page-content {
  max-width: 90%;
  width: auto;
  min-width: 300px;
  min-height: 450px; /* Altezza minima generale per schermi grandi */

  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #FFFFFF; /* Sfondo bianco pulito */
  color: #2c3e50; /* Testo blu scuro elegante */
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  align-items: center; /* Centra gli elementi orizzontalmente all'interno del flex container */
  justify-content: space-around; /* Distribuisce lo spazio equamente attorno agli elementi per un layout più bilanciato */

  position: relative;
  z-index: 1;
}

/* ==================================================
   MESSAGGIO
================================================== */
.slot-message {
  height: auto; /* L'altezza si adatta al contenuto del messaggio */
  min-height: 60px; /* Altezza minima per assicurare uno spazio adeguato */
  width: 100%;
  /* TESTO PIÙ GRANDE: Ho aumentato i valori di `clamp()` per un testo generalmente più grande.
     Puoi aggiustare questi numeri come preferisci:
     clamp(dimensione_minima, dimensione_viewport_relative, dimensione_massima) */
  font-size: clamp(1rem, 2.5vw, 1.3rem); /* Testo leggermente più grande e responsivo */
  color: #2c3e50; /* Testo del messaggio blu scuro */
  margin: 20px 0 0 0;
  padding: 0 10px;

  display: flex;
  align-items: center;
  justify-content: center;

  overflow-wrap: break-word;
  overflow: hidden;
  white-space: normal;
}

/* ==================================================
   HEADER IMAGE
================================================== */
h2 {
  margin-bottom: 10px;
  color: #FFC107; /* Giallo/Oro brillante per il titolo */
}

.slot-header-image {
  height: 250px;
  max-width: 100%;
  object-fit: contain;
  margin: 0 auto 30px; /* Centra l'immagine e aggiunge margine inferiore */

  cursor: pointer;
  transition: transform 0.3s ease;
  display: block;
}

.slot-header-image.zoomed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw !important;
  height: 100vh !important;
  object-fit: contain;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 10000;
  cursor: zoom-out;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 !important;
  border-radius: 0 !important;
  box-shadow: none !important;
  transform: none !important;
  pointer-events: all;
}

/* ==================================================
   RULLI
================================================== */
.slot-reels {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px; /* Spazio sotto i rulli */
}

.reel-symbol {
  font-size: clamp(2rem, 8vw, 3.5rem);
  background: #f8f9fa; /* Sfondo quasi bianco per i rulli */
  border-radius: 12px;
  width: clamp(55px, 15vw, 80px);
  height: clamp(55px, 15vw, 80px);
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Ombra più leggera */
  user-select: none;
  cursor: default;
  transition: transform 0.2s ease;
}

.reel-symbol:hover {
  transform: scale(1.1);
}

/* ==================================================
   MESSAGGIO VINCENTE
================================================== */
.winner-message {
  color: #28a745; /* Verde brillante per la vincita */
  text-shadow: 0 0 5px rgba(40, 167, 69, 0.5); /* Ombra verde più chiara */
  font-size: 1.1em;
}

/* ==================================================
   PULSANTE
================================================== */
.spin-button {
  background-color: #007bff; /* Blu primario brillante per il bottone */
  border: none;
  margin-top: 10px;
  margin-bottom: 0px; /* **Cruciale**: Nessun margine inferiore per evitare spazi indesiderati */
  padding: 15px 80px;
  border-radius: 30px;
  color: #fff;
  font-weight: 600;
  font-size: clamp(1rem, 3vw, 1.125rem);
  cursor: pointer;
  box-shadow: 0 6px 12px rgba(0, 123, 255, 0.4); /* Ombra blu più leggera */
  user-select: none;
  outline: none;
  transition:
    background-color 0.3s ease,
    color 0.3s ease,
    transform 0.2s ease;
  width: 90%;
  max-width: 350px;
}

.spin-button:disabled {
  background-color: #a3c4f3; /* Blu chiaro per bottone disabilitato */
  color: #eee;
  cursor: default;
  box-shadow: none;
}

.spin-button:not(:disabled):hover {
  background-color: #0056b3; /* Blu più scuro all'hover */
}

.spin-button:not(:disabled):active {
  transform: scale(0.95);
}

.spin-button:focus {
  outline: 3px solid #0056b3;
  outline-offset: 4px;
}

/* ==================================================
   MEDIA QUERIES
================================================== */

/* Schermi molto piccoli (fino a 400px) - Contenitore più responsivo */
@media (max-width: 400px) {
  .slot-page-content {
    width: 320px;
    height: auto; /* Altezza automatica per adattarsi al contenuto */
    min-height: 400px; /* Altezza minima per schermi molto piccoli */
    padding: 20px;
    padding-bottom: 10px;
    margin: 20px auto;
    overflow: hidden;
  }

  .slot-header-image {
    height: 150px;
    margin: 0 auto 15px; /* Centra e regola margine inferiore */
  }

  .slot-reels {
    gap: 8px;
  }

  .reel-symbol {
    font-size: 2.5rem;
    width: 50px;
    height: 50px;
  }

  .slot-message {
    height: auto; /* Altezza automatica per il messaggio */
    min-height: 55px; /* **AUMENTATO**: Altezza minima per il testo più grande */
    font-size: 1rem; /* **AUMENTATO**: Testo più grande sui cellulari piccoli */
    margin-bottom: 5px;
  }

  .spin-button {
    font-size: 1rem;
    padding: 10px 50px;
    margin-top: 10px;
    margin-bottom: 0px;
    width: 90%;
    max-width: 280px;
  }
}

/* Schermi medi (da 401px a 900px) - Contenitore più responsivo */
@media (min-width: 401px) and (max-width: 900px) {
  .slot-page-content {
    width: 380px;
    height: auto; /* Altezza automatica per adattarsi al contenuto */
    min-height: 500px; /* Altezza minima per schermi medi */
    padding: 20px;
    padding-bottom: 15px;
    margin: 20px auto;
    overflow: hidden;
  }

  .slot-header-image {
    height: 200px;
    margin: 0 auto 20px; /* Centra e regola margine inferiore */
  }

  .slot-reels {
    gap: 12px;
  }

  .reel-symbol {
    font-size: 3rem;
    width: 65px;
    height: 65px;
  }

  .slot-message {
    height: auto; /* Altezza automatica per il messaggio */
    min-height: 65px; /* **AUMENTATO**: Altezza minima per il testo più grande */
    font-size: 1.15rem; /* **AUMENTATO**: Testo più grande sui tablet/schermi medi */
    margin-bottom: 10px;
  }

  .spin-button {
    font-size: 1.1rem;
    padding: 12px 60px;
    margin-top: 15px;
    margin-bottom: 0px;
    width: 90%;
    max-width: 320px;
  }
}

/* Schermi grandi (>900px) - Comportamento originale per desktop */
@media (min-width: 901px) {
  .slot-page-content {
    max-width: 500px;
    width: 500px;
    padding: 50px;
    padding-bottom: 30px;
    min-width: 300px;
    min-height: 450px;
  }

  .slot-header-image {
    height: 300px;
    margin: 0 auto 30px; /* Centra e regola margine inferiore */
  }

  .slot-reels {
    gap: 20px;
  }

  .reel-symbol {
    font-size: 4rem;
    width: 80px;
    height: 80px;
  }

  .slot-message {
    height: auto;
    min-height: 70px; /* **AUMENTATO**: Altezza minima per il testo più grande */
    font-size: 1.25rem; /* **AUMENTATO**: Testo più grande sui desktop */
    margin-top: 20px; /* Assicurati che ci sia abbastanza spazio */
    margin-bottom: 20px;
  }

  .spin-button {
    padding: 20px 150px;
    font-size: 1.125rem;
    margin-top: 30px;
    margin-bottom: 0px;
    width: auto;
  }
}

/* ==================================================
   IMMAGINE PREMIO SWEET
================================================== */
/* Cascata caramelle */
.candy-cascade {
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
}

.falling-candy {
  position: absolute;
  top: -50px;
  width: 40px;
  height: 40px;
  animation: fall linear infinite;
}

@keyframes fall {
  0% {
    transform: translateY(-100px) rotate(0deg);
    opacity: 1;
  }
  100% {
    transform: translateY(100vh) rotate(360deg);
    opacity: 0;
  }
}